.navbar{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    padding: 5px;
    box-shadow: 0px 1px 3px gray;
}

.nav-logo{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.nav-logo p{
    font-size: 1.5rem;
    font-weight: 400;
    padding: 0px;
}

.nav-menu{
    list-style: none;
    display: flex;
    align-items: center;
    gap: 2rem;
    cursor: pointer;
}

.nav-menu hr{
    border: none;
    width: 70%rem;
    height: 3px;
    border-radius: 10px;
    background-color: rgba(243, 122, 70, 0.904);
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 2rem;
}
.nav-login-cart button{
    padding: 0rem 1px;
    font-size: 1rem;
    border-radius: 2rem;
    background-color: white;
    border-width: 1px;
    outline: none;
    height: 3rem;
    width: 5rem;
    cursor: pointer;
}
.nav-login-cart button:active{
    background: #f3f3f3 ;
}
.nav-cart-count{
    display: flex;
    height: 1rem;
    width: 1rem;
    align-items: center;
    justify-content: center;
    margin-top: -2rem;
    margin-left: -2.5rem;
    background-color: red;
    color: white;
    border-radius: 100%;
    font-size: 12px;
}