.offers {
  background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
  display: flex;
  width: 65%;
  height: 60vh;
  margin: auto;
  padding: 0px 140px;
  margin-bottom: 150px;
}

.offers-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.offers-left h1 {
  font-size: 50px;
  font-weight: 800;
}
.offers-left p {
  font-size: 15px;
  font-weight: 700;
}

.offers-left button {
  background-color: red;
  border-radius: 35px;
  padding: 15px;
  font-size: 15px;
  color: white;
  border: none;
  width: 30%;
  cursor: pointer;
  margin-top: 30px;
}

.offers-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 50px;
}
