.newcollections {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-top: 400px;
    margin-bottom: 100px;
  }
  .newcollections h1 {
    font-size: 3rem;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0px;
  }
  .collections{
    margin: 30px;
    display: flex;
    gap: 30px;
  }

  .newcollections hr {
    margin: 0px;
    width: 200px;
    border:3px solid;
    background: black;
    border-radius: 20px;
  }
  
  .collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr ;
    margin-top: 50px;
    gap: 30px;
  }