.item {
  width: 350px;
}
.item-prices {
  display: flex;
  gap: 2rem;
}
.old_price {
  color: rgb(168, 158, 158);
  text-decoration: line-through;
}
.new_price {
  font-weight: 500;
}
.item p {
  margin: 6px 0px;
}
.item:hover {
  transform: scale(1.05);
  transition: transform 0.6s ease-in-out;
}
