.hero {
  display: flex;
  background: linear-gradient(180deg, #fde1ff, #e1ffea22 60%);
}
.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
  padding-left: 12rem;
  line-height: 1.1;
  margin-top: -12rem;
}
.hero-left h2 {
  color: #090909;
  font-size: 3rem;
  font-weight: 600;
}

.hero-left p {
  font-size: 3rem;
  color: #090909;
  display: inline;
}

.hand-icon img {
  height: 4rem;
  width: 4rem;
  object-fit: cover;
}

.hero-latest-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  width: 310px;
  height: 70px;
  border-radius: 75px;
  margin-top: 30px;
  background-color: red;
  color: white;
  font-size: 22px;
  font-weight: 500;
}

.hero-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}