.popular {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  height: 90vh;
  margin: 5rem 0rem 10rem 0rem;
}
.popular-h1 {
  font-size: 2rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.popular-item {
  margin-top: 50px;
  display: flex;
  gap: 30px;
}
.popular hr {
  margin: 0px;
  width: 40%;
  height: 6px;
  background-color: black;
  border-radius: 20px;
}
.popular-h1 h1{
  margin: 0px;
}

